import React from 'react'
import { useSelector } from 'react-redux'
import { Image, Card } from 'semantic-ui-react'
import moment from 'moment'
import sources from '../sources'
import { isMobile } from 'react-device-detect'

const News = () => {
  const newsCountries1 = [
    'ae',
    'ar',
    'at',
    'au',
    'be',
    'bg',
    'br',
    'ca',
    'ch',
    'cn',
    'co',
    'cu',
    'cz',
    'de',
    'eg',
    'fr',
    'gb',
    'gr',
    'hk',
    'hu',
    'id',
    'ie',
    'il',
    'in',
    'it',
    'jp',
    'kr',
    'lt',
    'lv',
    'ma',
    'mx',
    'my',
    'ng',
    'nl',
    'no',
    'nz',
    'ph',
    'pl',
    'pt',
    'ro',
    'rs',
    'ru',
    'sa',
    'se',
    'sg',
    'si',
    'sk',
    'th',
    'tr',
    'tw',
    'ua',
    'us',
    've',
    'za',
  ]
  const state = useSelector((state) => state)
  // console.log('state.country', state.country)
  const countryNews = useSelector((state) => state.country.news.articles)
  // console.log('countryNews', countryNews)
  const selectedCountry = useSelector((state) =>
    state.countries.filtered[0].cca2.toLowerCase()
  )

  const findCountry = newsCountries1.includes(selectedCountry)
  //console.log('findCountry', findCountry)
  const findCountry2 = sources.filter(
    (source) => source.country === selectedCountry
  )
  if (!findCountry) {
    console.log('findCountry2', findCountry2)
  }
  return (
    <>
      <Card.Group itemsPerRow={isMobile ? 1 : 3}>
        {countryNews.map((item, i) => {
          const date = moment(item.publishedAt).format('YYYY-MM-DD LT')
          return (
            <Card raised as="a" href={item.url} key={i}>
              <Card.Header>{item.source.name} </Card.Header>
              <Image src={item.urlToImage} />
              <Card.Content>
                <Card.Header>{item.title} </Card.Header>
                <Card.Meta>{date} </Card.Meta>
                {item.author ? (
                  <Card.Meta>Author: {item.author} </Card.Meta>
                ) : (
                  <></>
                )}
                <Card.Description>{item.description} </Card.Description>
              </Card.Content>
            </Card>
          )
        })}
      </Card.Group>
    </>
  )
}

export default News
